@use '../util' as *;

.hero {
    height: calc(100vh - 40px);
    margin: rem(20) 5% 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include breakpoint-up(medium) {
        height: 100%;
        margin-bottom: 8%;
    }

    @include breakpoint-up(large) {
        max-width: rem(1004);
        margin: 0 auto 8%;
        padding: 15vh 0 0;
        flex-direction: row;
        align-items: start;
        gap: rem(100);
    }

    &__info {
        height: 100%;
        padding-top: 5%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        @include breakpoint-up(medium) {
            padding-top: 0;
        }
        @include breakpoint-up(large) {
            margin: auto;
            flex: 1 1 50%;
        }

        &--heading {
            @include breakpoint-up(large) {
                margin: auto;
            }
    
            h2 {
                margin-bottom: rem(10);
    
                @include breakpoint-up(medium) {
                    margin-bottom: rem(20);
                }
            }
            
            p {
                margin-bottom: rem(50);
                font-size: rem(20);
                line-height: 1.5;
    
                @include breakpoint-up(medium) {
                    margin-bottom: rem(80);
                    font-size: rem(18);
                }

                span {
                    color: var(--ltgreen);
                    font-weight: 500;
                }
            }
        }

        &--buttons {
            display: flex;
            align-self: start;
            flex-wrap: wrap;
            gap: rem(5);
           
            @include breakpoint-up(medium) {    
                a {
                    margin-right: rem(5);
                }
            }

        }
    }
    &__image {
        max-width: 350px;
        margin: rem(50) 0 rem(20);
        border-radius: 50%;
        flex: 1 1 75%;
        overflow: hidden;
        order: -1;
        opacity: 0.9;
        z-index: -1;

        @include breakpoint-up(large) {
            margin: 0;
            flex: 1 1 50%;
            order: 0;
        }

        img {
            width: 100%;
            height: 100%;
        }
    }
}